@import "colors.scss";

@mixin btn-outline-success {
  color: $color-secondary;
  border-color: $color-secondary;
  &:hover {
    color: $color-main-ad;
    background-color: $color-secondary;
    border-color: $color-main-ad;
  }
}


.btn-action{
  height: 30px;
  display: flex;
  &:focus{
    box-shadow: none;
  }
  img{
    height: 100%;
  }

}




//BUTTONS

.btn-primary {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
  &:hover {
    color: #fff;
    background-color: #0069d9;
    border-color: #0062cc;
  }
  &:focus, &.focus {
    color: #fff;
    background-color: #0069d9;
    border-color: #0062cc;
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5);
  }
  &.disabled, &:disabled {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
  }
}

